<template>
  <div class="article">
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0 search"
      placeholder="搜索" v-model="keywords"
    ></b-input>

     <div class="orderlist">
        <!--列表-->
         <div v-if="createdFilter(keywords).length > 0">
         <b-container class="bv-example-row">
             <div class="bountrytitle">正在销售的商品：</div>
          <b-row >
            <b-col sm="12" md="6" lg="4" v-for="order in createdFilter(keywords)">
              <div class="content" >
                <router-link :to="'/myseller/'+order.orderNumber">
                    <b-img :src="order.body | takeImage" fluid  class="orderimg"></b-img>
                </router-link>

                  <router-link :to="'/myseller/'+order.orderNumber">
                  <span class="title">{{ titleFormat(order.name) }}</span>
                </router-link>

                  <div class="text">
                      <span>卖家：@{{  order.nickname }} ({{ order.addr | formatAddr  }})</span><br>
                      买家： {{ order.buyers.length }} 个<br>
                      价格： {{ weiToEth(order.price)  }}  灵石<br>
                      <span>时间：{{ timeFormat(order.created) }} </span><br>
                      <!--<p>描述：{{  order.body | abstractFn }} ......</p>-->
                  </div>
              </div>
            </b-col>
          </b-row>
        </b-container>
         </div>
      </div>


          <!--列表-->
        <div class="orderlist" v-if="cancelledFilter(keywords).length > 0">
          <b-container class="bv-example-row">
              <div class="finishedtitle">已下架的商品：</div>
              <b-row >
                  <b-col sm="12" md="6" lg="4" v-for="order in cancelledFilter(keywords)">
                      <div class="content" >
                          <router-link :to="'/myseller/'+order.orderNumber">
                              <b-img :src="order.body | takeImage" fluid  class="orderimg"></b-img>
                          </router-link>

                          <router-link :to="'/myseller/'+order.orderNumber">
                              <span class="title">{{ titleFormat(order.name) }}</span>
                          </router-link>

                          <div class="text">
                              <span>卖家：@{{  order.nickname }} ({{ order.addr | formatAddr  }})</span><br>
                              买家： {{ order.buyers.length }} 个<br>
                              价格： {{ weiToEth(order.price)  }}  灵石<br>
                              <span>时间：{{ timeFormat(order.created) }} </span><br>
                              <!--<p>描述：{{  order.body | abstractFn }} ......</p>-->
                          </div>
                      </div>
                  </b-col>
              </b-row>
          </b-container>
          <div style="clear:both;"></div>
      </div>


      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

    <!--<div class="btn">-->
      <!--&lt;!&ndash;<b-button variant="outline-primary" @click="getNext">查看更多</b-button>&ndash;&gt;-->
    <!--</div>-->
  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
      name: "Myseller",
      data(){
          return{
            keywords:'',
            author:'',
            permlink:'',
            isLoading: true
          }
      },
      methods:{
        mysellerFilter(){
              let orders = this.$store.state.orders
              let mysellers = []
              orders.forEach(item => {
                  if(item.addr.toLowerCase() === this.$store.state.addr){
                      //是卖家
                      // console.log(99559, "是卖家")
                      mysellers.push(item)
                  }
              })
              return mysellers
          },
        createdFilter(keywords){
              let orders = this.mysellerFilter()
              let newarr = []
              orders.forEach(item => {
                  if(item.state === '0' && item.name.includes(keywords)){
                      newarr.push(item)
                  }
              })
              return newarr
          },
        cancelledFilter(keywords){
            let orders = this.mysellerFilter()
            let newarr = []
            orders.forEach(item => {
                if(item.state === '1' && item.name.includes(keywords)){
                    newarr.push(item)
                }
            })
            return newarr
        },
        getNext(){
          let that = this
          that.isLoading = true
          let query = {tag: "hive-180932", limit: 20, start_author: this.author, start_permlink: this.permlink}
          this.steem.api.getDiscussionsByTrending(query, function(err, data) {
            data.forEach(post => {
              if (post.permlink != that.permlink) {
                that.posts.push(post)
              }
            })
            //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
            that.author = data[data.length - 1].author
            that.permlink = data[data.length - 1].permlink
            //将得到的数据存入vuex中
            that.$store.commit('saveHot', that.posts)
            that.isLoading = false
            if(data.length == 1){
              alert('没有更多了！')
            }
          })
        },
      },

      components: {
        Loading
      },

        mounted() {
            let that = this
            let instance = this.$store.state.ordertinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    // console.log(868, "instance为空，是刷新页面")
                    instance = await that.getOrderInstance()
                    await that.getOrders(instance)
                    that.isLoading = false
                } else{
                    if(that.$store.state.orders == false){
                        // console.log(667, "如果文章更新！")
                        //如果文章更新
                        await that.getOrders(instance)
                        that.isLoading = false
                    } else{
                        // console.log(2233, "啥也没干！")
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{
        formatAddr(data){
          return data.substring(0,6)+'...'+data.substring(38)
        },
        takeImage(text){
          let reg = /!\[(.*?)\]\((.*?)\)/
          // let reg = /\!\[.*?\)/
          let a = text.match(reg)
          if(a === null){
              return 'https://steemjiang.com:8081/ipfs/QmegAj9BdJ1HGhZSJtFs4WUAen82aMQtcbnoxujd6YMQ5o'
          }
          return a[2]
        },
        abstractFn(res){
          if(!res){
            return ''
          }else{
            let str=res.replace(/(\*\*|__)(.*?)(\*\*|__)/g,'')          //全局匹配内粗体
                    .replace(/\!\[[\s\S]*?\]\([\s\S]*?\)/g,'')           //全局匹配图片
                    .replace(/\([\s\S]*?\)/g,'')                              //全局匹配连接
                    .replace(/<\/?.+?\/?>/g,'')                             //全局匹配内html标签
                    .replace(/http.*\w/g,'')                              //全局http连接
                    .replace(/(\*)(.*?)(\*)/g,'')                               //全局匹配内联代码块
                    .replace(/`{1,2}[^`](.*?)`{1,2}/g,'')                       //全局匹配内联代码块
                    .replace(/```([\s\S]*?)```[\s]*/g,'')                       //全局匹配代码块
                    .replace(/\~\~(.*?)\~\~/g,'')                               //全局匹配删除线
                    .replace(/[\s]*[-\*\+]+(.*)/g,'')                           //全局匹配无序列表
                    .replace(/[\s]*[0-9]+\.(.*)/g,'')                           //全局匹配有序列表
                    .replace(/(#+)(.*)/g,'')                                    //全局匹配标题
                    .replace(/(>+)(.*)/g,'')                                    //全局匹配摘要
                    .replace(/\r\n/g,"")                                        //全局匹配换行
                    .replace(/\n/g,"")                                          //全局匹配换行
            // .replace(/\s/g,"")                                          //全局匹配空字符;
            return str.slice(0,100)
          }
        },
      },
    }
</script>

