<script>
  import Loading from './Loading'
    export default {
        name: "MyTask",
      data(){
          return{
            keywords:'',
            author:'',
            permlink:'',
            isLoading: true
          }
      },
      methods:{
        applied(keywords){
            let bountys = this.$store.state.bountys
            return bountys.filter(item => {
                  if(item.state === '0' && item.name.includes(keywords) ){
                      for(let i = 0; i < item.hunters.length; i ++){
                          if(item.hunters[i].toLowerCase() === this.$store.state.addr){
                              return item
                          }
                      }
                  }
              })
          },
        failtobechosen(keywords){
              let bountys = this.$store.state.bountys
              let addr = this.$store.state.addr
              return bountys.filter(item => {
                  if(item.state !== '0' && item.hunterA.toLowerCase() !== addr  && item.name.includes(keywords) ){
                      for(let i = 0; i < item.hunters.length; i ++){
                          if(item.hunters[i].toLowerCase() === addr){
                              return item
                          }
                      }
                  }
              })
          },
        myTaskFilter(){
            let bountys = this.$store.state.bountys
            let mytask = []
            bountys.forEach(item => {
                // console.log(699, item.hunterA)
                if(item.hunterA.toLowerCase() === this.$store.state.addr){
                    //是中选猎人
                    // console.log(999, "中选猎人")
                    mytask.push(item)
                }
            })
            return mytask
          },
        confirmed(keywords){
              let bountys = this.myTaskFilter()
              return bountys.filter(item => {
                  if(item.state === '2' && item.name.includes(keywords)){
                      return item
                  }
              })
          },
        hunterFinished(keywords){
              let bountys = this.myTaskFilter()
              return bountys.filter(item => {
                  if(item.state === '3' && item.name.includes(keywords)){
                      return item
                  }
              })
          },
        promoterFinished(keywords){
              let bountys = this.myTaskFilter()
              return bountys.filter(item => {
                  if(item.state === '5' && item.name.includes(keywords)){
                      return item
                  }
              })
          },
        otherFinished(keywords){
              let bountys = this.myTaskFilter()
              return bountys.filter(item => {
                  if(item.state === '4' || item.state === '6' && item.name.includes(keywords)){
                      return item
                  }
              })
          },
        getNext(){
          let that = this
          that.isLoading = true
          let query = {tag: "hive-180932", limit: 20, start_author: this.author, start_permlink: this.permlink}
          this.steem.api.getDiscussionsByTrending(query, function(err, data) {
            data.forEach(post => {
              if (post.permlink != that.permlink) {
                that.posts.push(post)
              }
            })
            //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
            that.author = data[data.length - 1].author
            that.permlink = data[data.length - 1].permlink
            //将得到的数据存入vuex中
            that.$store.commit('saveHot', that.posts)
            that.isLoading = false
            if(data.length == 1){
              alert('没有更多了！')
            }
          })
        },
      },

      components: {
        Loading
      },

       mounted() {
            let that = this
            let instance = this.$store.state.bountyinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    // console.log(888, "instance为空，是刷新页面")
                    let instance = await that.getBountyInstance()
                    await that.getTasks(instance)
                    that.isLoading = false
                } else{
                    if(that.$store.state.bountys == false){
                        // console.log(6647, "如果悬赏更新！")
                        //如果文章更新
                        await that.getTasks(instance)
                        that.isLoading = false
                    } else{
                        // console.log(22333, "啥也没干！")
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{
        formatAddr(data){
          return data.substring(0,6)+'...'+data.substring(38)
        },
        takeImage(text){
          let reg = /!\[(.*?)\]\((.*?)\)/
          // let reg = /\!\[.*?\)/
          let a = text.match(reg)
          if(a === null){
            return 'https://steemjiang.com:8081/ipfs/QmegAj9BdJ1HGhZSJtFs4WUAen82aMQtcbnoxujd6YMQ5o'
          }
          return a[2]
        },
        abstractFn(res){
          if(!res){
            return ''
          }else{
            let str=res.replace(/(\*\*|__)(.*?)(\*\*|__)/g,'')          //全局匹配内粗体
                    .replace(/\!\[[\s\S]*?\]\([\s\S]*?\)/g,'')           //全局匹配图片
                    .replace(/\([\s\S]*?\)/g,'')                              //全局匹配连接
                    .replace(/<\/?.+?\/?>/g,'')                             //全局匹配内html标签
                    .replace(/http.*\w/g,'')                              //全局http连接
                    .replace(/(\*)(.*?)(\*)/g,'')                               //全局匹配内联代码块
                    .replace(/`{1,2}[^`](.*?)`{1,2}/g,'')                       //全局匹配内联代码块
                    .replace(/```([\s\S]*?)```[\s]*/g,'')                       //全局匹配代码块
                    .replace(/\~\~(.*?)\~\~/g,'')                               //全局匹配删除线
                    .replace(/[\s]*[-\*\+]+(.*)/g,'')                           //全局匹配无序列表
                    .replace(/[\s]*[0-9]+\.(.*)/g,'')                           //全局匹配有序列表
                    .replace(/(#+)(.*)/g,'')                                    //全局匹配标题
                    .replace(/(>+)(.*)/g,'')                                    //全局匹配摘要
                    .replace(/\r\n/g,"")                                        //全局匹配换行
                    .replace(/\n/g,"")                                          //全局匹配换行
            // .replace(/\s/g,"")                                          //全局匹配空字符;
            return str.slice(0,100)
          }
        },
      },
    }
</script>

