<template>
  <div class="login">
    <h2>钱包</h2>
    <div class="exchange">
      <hr>
     </div>
    <div v-if="flag">
      <div class="text">
      <!--总量： {{ totalSupply }} <br>-->
      呢称： {{ $store.state.nickname }}<br>
      地址： {{ $store.state.addr }} <br>
      以太坊余额： {{ balanceOfEth }}<br>
      灵石余额：  {{ balanceOf }}<br><br>

      <!--授权： {{ $store.state.allowance }}<br><br>-->
      </div>

      <!--转帐-->
      <span style="margin-right: 1rem">
        <b-button variant="outline-primary" @click="beforetransfer">转帐</b-button>
      </span>
      <span style="margin-right: 1rem">
      <b-button variant="outline-primary" @click="transferHistory">转帐历史</b-button>
       </span>
      <span style="margin-right: 1rem">
      <b-button variant="outline-primary" @click="beforeexchange">兑换</b-button>
        </span>
      <span style="margin-right: 1rem">
      <b-button variant="outline-primary" @click="beforecheck">查询</b-button>
      </span>


      <!--转帐-->
      <div v-if="transferFlag">
        <br>
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user"
                placeholder="转帐地址" v-model="to"
        ></b-input>
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user"
                placeholder="灵石数量" v-model="value"
        ></b-input>
        <div class="loginbtn">
          <b-button variant="outline-primary" @click="transfer">确认转帐</b-button>
        </div>
        <br><br>
      </div>

      <!--查询转帐历史-->
      <div v-if="historyFlag" class="text2">
        <br>
        <h6>转入帐单：</h6>
        <div v-for="a in transferIn" class="onetransfer">
          <li class="time">{{ a.time }} </li>
          <span style="margin-right: 0.5rem;">
            对方帐户：{{ a.returnValues.from }}</span>
           转入灵石：+ {{ weiToEth(a.returnValues.value) }} 灵石
        </div>
        <br>

        <h6>转出帐单：</h6>
        <div v-for="b in transferOut" class="onetransfer">
          <li class="time">{{ b.time }} </li>
          <span style="margin-right: 0.5rem;">
            对方帐户：{{ b.returnValues.to }}</span>
            转出灵石：- {{ weiToEth(b.returnValues.value) }} 灵石<br>
        </div>
      </div>


      <!--兑换-->
      <div v-if="exchangeFlag" class="exchange"><br>
        兑换率： 1以太坊 = {{ price }}灵石   <br><hr>
         <p>以太坊兑换灵石，可兑换灵石上限： {{ balanceOfContract }}</p>
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user2"
                placeholder="以太坊数量" v-model="ethvalue"
        ></b-input>以太坊
        <span v-if="ethvalue" style="margin-left: 1rem; color: rgba(240,56,6,0.75)">
          可兑换 {{ ethvalue * price }} 灵石
        </span>
        <div class="loginbtn">
          <b-button variant="outline-primary" @click="ethToLark">确认兑换灵石</b-button>
        </div>
        <hr>

        <p>灵石兑换以太坊，可兑换以太坊上限： {{ ethOfContract }} </p>
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user2"
                placeholder="灵石数量" v-model="larkvalue"
        ></b-input>灵石
        <span v-if="larkvalue" style="margin-left: 1rem; color: rgba(240,56,6,0.75)">
         可兑换 {{ larkvalue / price }} 以太坊
        </span>
        <div v-if="isApprove" >
          <div v-if="approveFlag">
          <b-button variant="outline-primary" @click="letapprove" class="loginbtn btn">授权 </b-button>
          <span style="color: gray;padding-left: 1rem;">确认兑换以太坊</span><br><br>
          </div>
          <div class="btn3" v-else>您已提交，请等待网络确认！</div>
        </div>
        <div class="loginbtn" v-else>
          <b-button variant="outline-primary" @click="larkToEth">确认兑换以太坊</b-button>
        </div>
      </div>


      <!--查询-->
      <div v-if="checkFlag">
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user"
                placeholder="地址" v-model="anyaddr"
        ></b-input>
        <span v-if="ischeck">灵石余额: {{ anyvalue }}</span>
        <div class="loginbtn">
          <b-button variant="outline-primary" @click="anybalance">查询余额</b-button>
        </div>
      </div>

    </div>
    <!--加载动画-->
    <transition name="fade">
      <SmallLoading v-if="isLoading"></SmallLoading>
    </transition>
  </div>
</template>

<script>
  import SmallLoading from './SmallLoading'
  export default {
    name: "Login",
    data() {
      return {
        balanceOf: '',
        password:'',
        isLoading: true,
        flag: false,
        totalSupply: '',
        to:'',
        value:'',
        transferFlag: false,
        historyFlag: false,
        transferIn: [],
        transferOut: [],

        exchangeFlag: false,
        checkFlag: false,
        anyaddr:'',
        anyvalue:'',
        balanceOfEth: '',
        ethvalue:'',
        larkvalue:'',
        balanceOfContract:'',
        ethOfContract: '',
        price:'',
        ischeck: false,

        balanceOfEth2: '',
        balanceOf2: '',
        balanceOfContract2:'',
        ethOfContract2: '',

        approveFlag: true
      }
    },
    methods: {
      async letapprove(){
        this.approveFlag = false
        await this.approve()
        await this.getBalanceAndAllowance()
      },
      async getBalance(){
        let addr = this.$store.state.addr
        let instance = this.$store.state.walletinstance
        let c = await this.web3.eth.getBalance(addr)
        this.balanceOfEth = this.weiToEth(c)
        this.balanceOfEth2 = this.web3.utils.fromWei(c, 'ether')

        let a = await instance.balanceOf(addr)
        this.balanceOf = this.weiToEth(a)
        this.balanceOf2 = this.web3.utils.fromWei(a, 'ether')

        let b = await instance.totalSupply()
        this.totalSupply = this.weiToEth(b)
      },
      async getExchangeDone(){
        let instance = this.$store.state.walletinstance
        let instance2 = this.$store.state.exchangeinstance
        //查询兑换合约中的余额
        let contractaddr = await instance2.getThis()
        //灵石余额
        let a3 = await instance.balanceOf(contractaddr)
        this.balanceOfContract = this.weiToEth(a3)
        this.balanceOfContract2 = this.web3.utils.fromWei(a3, 'ether')

        //以太币余额
        let a4 = await this.web3.eth.getBalance(contractaddr)
        this.ethOfContract = this.weiToEth(a4)
        this.ethOfContract2 = this.web3.utils.fromWei(a4, 'ether')

        //兑换价格
        let a5 = await instance2.getMessage()
        this.price = a5[1]
      },
      beforetransfer() {
        this.exchangeFlag = false
        this.checkFlag = false
        this.ischeck = false
        this.historyFlag = false
        this.anyaddr = ''
        this.anyvalue = ''
        this.transferFlag = !this.transferFlag
      },
      beforeexchange() {
        this.transferFlag = false
        this.checkFlag = false
        this.ischeck = false
        this.historyFlag = false
        this.anyaddr = ''
        this.anyvalue = ''
        this.exchangeFlag = !this.exchangeFlag
      },
      beforecheck() {
        this.transferFlag = false
        this.exchangeFlag = false
        this.ischeck = false
        this.historyFlag = false
        this.checkFlag = !this.checkFlag
        this.anyaddr = ''
        this.anyvalue = ''
      },
      async transfer() {
        await this.unlockWallet()
        this.isLoading = true
        this.transferFlag = false
        let value3 = this.web3.utils.toWei(this.value, 'ether')
        let addr = this.$store.state.addr
        let instance = this.$store.state.walletinstance

        await instance.transfer(this.to, value3, {from: addr, gasPrice: this.gasPrice})
        await this.getBalance()
        await this.getExchangeDone()
        this.value = ''
        this.to = ''
        this.isLoading = false
      },
      async transferHistory(){
        this.isLoading = true
        this.exchangeFlag = false
        this.checkFlag = false
        this.transferFlag = false
        this.ischeck = false
        this.anyaddr = ''
        this.anyvalue = ''

        let addr = this.$store.state.addr
        let instance = this.$store.state.walletinstance
        let a = await instance.getPastEvents('Transfer', {filter: {to: addr}, fromBlock: 0, toBlock: 'latest'})
        for(let i = a.length -1; i >= 0; i--){
          this.transferIn[a.length-1-i] = a[i]
          let s = await this.web3.eth.getBlock(a[i].blockNumber)
          let s2 = this.timeFormat(s.timestamp)
          this.transferIn[a.length-1-i].time = s2
        }

        let b = await instance.getPastEvents('Transfer', {filter: {from: addr}, fromBlock: 0, toBlock: 'latest'})
        for(let i = b.length -1; i >= 0; i--){
          this.transferOut[b.length-1-i] = b[i]
          let s3 = await this.web3.eth.getBlock(b[i].blockNumber)
          let s4 = this.timeFormat(s3.timestamp)
          this.transferOut[b.length-1-i].time = s4
        }
        this.isLoading = false
        this.historyFlag = true
      },
      async anybalance() {
        let addr = this.anyaddr
        let instance = this.$store.state.walletinstance
        let s = await instance.balanceOf(addr)
        this.anyvalue = this.weiToEth(s)
        this.ischeck = true
      },
      async larkToEth() {
        await this.unlockWallet()
        if(parseFloat(this.balanceOf2) > parseFloat(this.larkvalue)){
          if(parseFloat(this.larkvalue / this.price) < parseFloat(this.ethOfContract2)){
            this.isLoading = true
            this.exchangeFlag = false
            let addr = this.$store.state.addr
            let instance = this.$store.state.exchangeinstance
            let value = this.web3.utils.toWei(this.larkvalue, 'ether')
            await instance.larkToEth(value, {from: addr, gasPrice: this.gasPrice})
            await this.getBalance()
            await this.getExchangeDone()
            await this.getBalanceAndAllowance()
            this.larkvalue = ''
            this.isLoading = false
          }else{
            alert("您已超出可兑换以太坊上限！")
          }
        }else{
          alert("您的灵石余额不足！")
        }
      },
      async ethToLark() {
        await this.unlockWallet()
        if(parseFloat(this.balanceOfEth2) > parseFloat(this.ethvalue)){
          if(parseFloat(this.ethvalue * this.price) < parseFloat(this.balanceOfContract2)){
            this.isLoading = true
            this.exchangeFlag = false
            let addr = this.$store.state.addr
            let instance = this.$store.state.exchangeinstance
            let value = this.web3.utils.toWei(this.ethvalue, 'ether')
            let contract = await instance.getThis()
            await this.web3.eth.sendTransaction({from: addr, to: contract, value: value, gasPrice: this.gasPrice})
            await this.getBalance()
            await this.getExchangeDone()
            await this.getBalanceAndAllowance()
            this.ethvalue = ''
            this.isLoading = false
          }else{
            alert("您已超出可兑换灵石上限！")
          }
        }else{
          alert("您的以太坊余额不足！")
        }

      },

    },
    computed: {
      isApprove() {
        return  parseFloat(this.$store.state.allowance) < parseFloat(this.larkvalue)
      },
    },
    components: {
      SmallLoading
      },

    mounted() {
      let that = this
      let instance = this.$store.state.walletinstance
      async  function main(){
        if(Object.keys(instance).length === 0){
          //如果刷新页面, instance未定义
          // console.log(888, "instance为空，是刷新页面")
          await that.getWalletInstance()
          await that.getExchangeInstance()
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        } else{
          // console.log(22333, "啥也没干！")
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        }

        //查询余额和授权
        if(that.$store.state.allowance === ''){
          // console.log(111, "查授权")
          await that.getBalanceAndAllowance()
        }else{
          // console.log(222, "已查授权")
        }
      }
      main()
    },

    }
</script>

<style scoped>

  .login{
    margin: 4rem 4rem 4rem 15rem;
    /*background-color: aqua;*/
  }
  .user{
    margin-top: 1rem;
    width: 30rem;
