<script>
    import Loading from './Loading'
    export default {
        name: "MyBounty",
        data(){
            return{
                keywords:'',
                isLoading: true,
                historyFlag: false,
                searchBountyFlag: false,
                bountys: []
            }
        },
        methods:{
            myBountyFilter(){
                let bountys = this.$store.state.bountys
                let mybounty = []
                bountys.forEach(item => {
                    if(item.promoter.toLowerCase() === this.$store.state.addr){
                        //是金主
                        // console.log(999, "是金主")
                        mybounty.push(item)
                    }
                })
                return mybounty
            },
            createdFilter(keywords){
                let bountys = this.myBountyFilter()
                return bountys.filter(item => {
                    if(item.state === '0' && item.name.includes(keywords)){
                        return item
                    }
                })
            },
            confirmed(keywords){
                let bountys = this.myBountyFilter()
                return bountys.filter(item => {
                    if(item.state === '2' && item.name.includes(keywords)){
                        return item
                    }
                })
            },
            hunterFinished(keywords){
                let bountys = this.myBountyFilter()
                return bountys.filter(item => {
                    if(item.state === '3' && item.name.includes(keywords)){
                        return item
                    }
                })
            },
            promoterFinished(keywords){
                let bountys = this.myBountyFilter()
                return bountys.filter(item => {
                    if(item.state === '5' && item.name.includes(keywords)){
                        return item
                    }
                })
            },
            otherFinished(keywords){
                let bountys = this.myBountyFilter()
                return bountys.filter(item => {
                    if(item.state === '4' || item.state === '6' && item.name.includes(keywords)){
                        return item
                    }
                })
            },
            cancelledFilter(keywords){
                let bountys = this.myBountyFilter()
                return bountys.filter(item => {
                    if(item.state === '1' && item.name.includes(keywords)){
                        return item
                    }
                })

            },
            searchHistory(){
                this.historyFlag = !this.historyFlag
            },
            async searchBounty(){
                this.searchBountyFlag = !this.searchBountyFlag
                let addr = this.$store.state.addr
                let instance = this.$store.state.bountyinstance
                let res = await instance.getPastEvents('SetTask', {fromBlock: 8468674, toBlock: 'latest'})
                // let res = await instance.getPastEvents('SetTask', {filter: {uid: 3}, fromBlock: 0, toBlock: 'latest'})
                console.log(77, res)
                this.bountys = res
            },
            async searchCancel(){
                let addr = this.$store.state.addr
                let instance = this.$store.state.bountyinstance
                let res = await instance.getPastEvents('CancelTask', {filter: {promoter: addr}, fromBlock: 0, toBlock: 'latest'})
                console.log(88, res)
            },
        },

        components: {
            Loading
        },

        mounted() {
            let that = this
            let instance = this.$store.state.bountyinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    // console.log(888, "instance为空，是刷新页面")
                    let instance = await that.getBountyInstance()
                    await that.getTasks(instance)
                    that.isLoading = false
                } else{
                    if(that.$store.state.bountys == false){
                        // console.log(6647, "如果悬赏更新！")
                        //如果文章更新
                        await that.getTasks(instance)
                        that.isLoading = false
                    } else{
                        // console.log(22333, "啥也没干！")
                        that.isLoading = false
                    }
                }
            }
            main()
        },

        filters:{
            formatAddr(data){
                return data.substring(0,6)+'...'+data.substring(38)
            },
            takeImage(text){
                let reg = /!\[(.*?)\]\((.*?)\)/
                // let reg = /\!\[.*?\)/
                let a = text.match(reg)
                if(a === null){
                    return 'https://steemjiang.com:8081/ipfs/QmegAj9BdJ1HGhZSJtFs4WUAen82aMQtcbnoxujd6YMQ5o'
                }
                return a[2]
            },
            abstractFn(res){
                if(!res){
                    return ''
                }else{
                    let str=res.replace(/(\*\*|__)(.*?)(\*\*|__)/g,'')          //全局匹配内粗体
                        .replace(/\!\[[\s\S]*?\]\([\s\S]*?\)/g,'')           //全局匹配图片
                        .replace(/\([\s\S]*?\)/g,'')                              //全局匹配连接
                        .replace(/<\/?.+?\/?>/g,'')                             //全局匹配内html标签
                        .replace(/http.*\w/g,'')                              //全局http连接
                        .replace(/(\*)(.*?)(\*)/g,'')                               //全局匹配内联代码块
                        .replace(/`{1,2}[^`](.*?)`{1,2}/g,'')                       //全局匹配内联代码块
                        .replace(/```([\s\S]*?)```[\s]*/g,'')                       //全局匹配代码块
                        .replace(/\~\~(.*?)\~\~/g,'')                               //全局匹配删除线
                        .replace(/[\s]*[-\*\+]+(.*)/g,'')                           //全局匹配无序列表
                        .replace(/[\s]*[0-9]+\.(.*)/g,'')                           //全局匹配有序列表
                        .replace(/(#+)(.*)/g,'')                                    //全局匹配标题
                        .replace(/(>+)(.*)/g,'')                                    //全局匹配摘要
                        .replace(/\r\n/g,"")                                        //全局匹配换行
                        .replace(/\n/g,"")                                          //全局匹配换行
                    // .replace(/\s/g,"")                                          //全局匹配空字符;
                    return str.slice(0,100)
                }
            },
        },
    }
</script>

