const approve = async function(){
      await this.unlockWallet()
      let addr = this.$store.state.addr
      let instance = this.$store.state.walletinstance
      //授权额度
      let amount = this.web3.utils.toWei('80000000', 'ether')
      let c = await instance.approve(this.tokenproxy, amount , {from: addr, gasPrice: this.gasPrice})
      console.log(1234, c, "approve")
}

export {approve}
