import axios from 'axios'
import TruffleContract from 'truffle-contract'
import Web3 from 'web3'
import abi from 'ethereumjs-abi'
import blacklists from "../public/blackLists.json"
import gasprice from "../public/gasprice.json"
import CryptoJS from 'crypto-js'

import {getstr} from "./utils/getstr"
import {getNum} from "./utils/getNum"
import {timeFormat} from "./utils/timeFormat"
import {formatSeconds} from "./utils/formatSeconds"
import {titleFormat} from "./utils/titleFormat"
import {getTimeStamp} from "./utils/getTimeStamp"
import {utf8ToStr} from "./utils/utf8ToStr"
import {weiToEth} from "./utils/weiToEth"
import {getPostInstance} from "./utils/getPostInstance"
import {getArticles} from "./utils/getArticles"
import {getOrderInstance} from "./utils/getOrderInstance"
import {getOrders} from "./utils/getOrders"
import {getBountyInstance} from "./utils/getBountyInstance"
import {getTasks} from "./utils/getTasks"
import {getWalletInstance} from "./utils/getWalletInstance"
import {getExchangeInstance} from "./utils/getExchangeInstance"
import {getBalanceAndAllowance} from "./utils/getBalanceAndAllowance"
import {approve} from "./utils/approve"
import {imgAdd} from "./utils/imgAdd"
import {initDatabase} from "./utils/initDatabase"
import {getAllPosts} from "./utils/getAllPosts"
import {getCollections} from "./utils/getCollections"
import {saveOneToDb} from "./utils/saveOneToDb"
