Vue.use(BootstrapVue)
Vue.use(mavonEditor)

//获取黑名单
let blackLists = blacklists.trim().split(/\s+/)

//获取gasPrice
let gasPrice = gasprice.gasPrice

let web3
let web3Provider
if (typeof window.ethereum !== 'undefined'){
  console.log(111, "metamask")
  web3Provider = window.ethereum
  // console.log(122, web3Provider)
  web3 = new Web3(window.ethereum)
  // console.log(123, web3)
} else {
  console.log(222, "无metamask")
  alert("没有安装metamask！\n 请安装！ https://metamask.io ")
}

//设置ipfs节点, ,挂载到全局
const ipfs = ipfsClient({ host: 'steemjiang.com', port: '9001', protocol: 'https' })
Vue.prototype.ipfs = ipfs

const ethapi = require('etherscan-api').init('DYX36PF9I32G7TDQDFF97CSK24SXWZXIN9','ropsten',  '3000')

//界主
const s = '0x9b6043F20454a1071534F8a35B3ad391512102F5'
const owner = s.toLowerCase()
//tokenproxy
const tokenproxy = '0x7Ef4BcC1d24086Dd867Cd132d850E41a2B596052'

//加密秘钥 必须为：8/16/32位
let aseKey = "a2c4J6vY"
//加密函数
function encypt(message){
  return CryptoJS.AES.encrypt(message, CryptoJS.enc.Utf8.parse(aseKey), {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7
  }).toString()
}
//解密函数
function decrypt(code){
  return CryptoJS.AES.decrypt(code, CryptoJS.enc.Utf8.parse(aseKey), {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7
  }).toString(CryptoJS.enc.Utf8)
}

Vue.prototype.getstr = getstr
Vue.prototype.getNum = getNum
Vue.prototype.timeFormat = timeFormat
Vue.prototype.formatSeconds = formatSeconds
Vue.prototype.titleFormat = titleFormat
