<template>
  <div id="content">
    <h1>{{ article.title }}</h1>

    <p class="posttime">
      @{{  article.nickname }}({{ article.addr }})
      <span style="margin-left: 1rem"> {{ timeFormat(article.created) }}</span>
    </p>

    <div id="textclass">
    <mavon-editor
     :value="article.body  + '<br>' + '<br>' + keywords + article.keywords.join('　')"
    :subfield = "prop.subfield"
    :defaultOpen = "prop.defaultOpen"
    :toolbarsFlag = "prop.toolbarsFlag"
    :editable="prop.editable"
    :scrollStyle="prop.scrollStyle"
    ></mavon-editor>
    </div>
      </div>
</template>

<script>
 import SmallLoading from './SmallLoading'
 export default {
      name: "Trend",
      data() {
        return {
          db:{},
          article: {},
          data:'',
          text:'',
          keywords: '关键词：　',
        }
      },

      methods:{
        articleFilter(){
          //获取单篇文章
          let articles = this.$store.state.articles
          let permlink = this.$route.params.permlink
          articles.forEach(item => {
            if(item.permlink === permlink){
              this.article = item
            }
          })
        },
        addCollection() {
          let db = this.$store.state.db
          const collection = this.$store.state.username + 'collection'
          // console.log(888, db, '版本号', db.version)

          //得到操作 'collection' 表的事务对象
          let transaction = db.transaction([collection], 'readwrite') //可读可写事务

          transaction.oncomplete = function (event) {
            console.log("事务操作完成")
          }
          transaction.onerror = function (event) {
            console.log("事务操作出错")
            console.dir(event)
          }
          transaction.onsuccess = function (event) {
            console.log('事务操作成功')
          }

          //从事务对象里得到表里的objectStore对象
          let objectStore = transaction.objectStore(collection)
          let post = this.post
          objectStore.add(post)
          // console.log(456, "收藏成功")

          //设置收藏标记
          let collectionFlag = this.$route.params.permlink
          localStorage.setItem(collectionFlag, 'steemjiang')
          this.flag = localStorage.getItem(this.$route.params.permlink)

        },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        },
      },

      computed: {
        prop () {
          let data = {
            subfield: false,
            defaultOpen: 'preview',
            editable: false,
            toolbarsFlag: false,
            scrollStyle: false,

          }
          return data
        },
      },

      mounted() {
        let that = this
        let instance = this.$store.state.postinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //如果刷新页面, instance未定义
            // console.log(777, "instance为空，是刷新页面")
            let instance = await that.getPostInstance()
            await that.getArticles(instance)
            that.articleFilter()
            that.isLoading = false
          } else{
            if(that.$store.state.articles == false){
              // console.log(666, "如果文章更新！")
              //如果文章更新
              await that.getArticles(instance)
              that.articleFilter()
              that.isLoading = false
            } else{
              // console.log(2222, "啥也没干！")
              that.articleFilter()
              that.isLoading = false
            }
          }

            //查询余额和授权
            if(that.$store.state.allowance === ''){
                // console.log(111, "查授权")
                await that.getBalanceAndAllowance()
            }else{
                // console.log(222, "已查授权")
            }

        }
        main()
      },

      filters:{
        formatAddr(data){
          return data.substring(0,6)+'...'+data.substring(38)
        },

      },

     components: {
         SmallLoading
     },
    }
</script>

<style scoped>
  h1{
    font-size: 2rem;
    margin: 1.5rem auto;
    text-align: center;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ddd;
  }

  .posttime{
    font-size: 0.8rem;
    text-align: center;
  }

  #content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 1rem auto;
    width: 60%;
    padding-bottom: 3rem;
    position:relative;
    z-index: 100;
   }

  .v-note-panel{
    background-color: aquamarine !important;
    font-size: 2rem;
    z-index: -1 !important;
  }

  #textclass{
    z-index: auto;
    background-color: aquamarine !important;
  }


  .vote{
    text-align: center;
  }



</style>
