<template>
  <div class="login">
    <div v-if="owner === $store.state.addr">
      <h3 >欢迎您，界主大人！</h3>
      <div v-if="flag">
        呢称： {{ $store.state.nickname }}<br>
        地址： {{ $store.state.addr }} <br>
        灵石总量： {{ totalSupply }} <br><br><br>

         兑换率： {{ price }} （1以太币 = {{ price }}灵石 ）<br>
         重设汇率:
          <b-input
                    class="mb-2 mr-sm-2 mb-sm-0 user"
                    placeholder="输入新汇率" v-model="newPrice"
            ></b-input>
            <div class="loginbtn">
              <b-button variant="outline-primary" @click="setPrice">重设汇率 </b-button>
            </div>
            <br><br>

            提取兑换合约灵石： <br>
            <b-input
                    class="mb-2 mr-sm-2 mb-sm-0 user"
                    placeholder="输入数量" v-model="withdrawAmount"
            ></b-input>
            <div class="loginbtn">
              <b-button variant="outline-primary" @click="withdrawLark">提取灵石 </b-button>
            </div>

      </div>
      <!--加载动画-->
      <transition name="fade">
        <loading v-if="isLoading"></loading>
      </transition>
    </div>
  </div>
</template>

<script>
  import Loading from './Loading'
  export default {
    name: "Admin",
    data() {
      return {
        isLoading: false,
        flag: false,
        addAmount: '',
        price:'',
        newPrice: '',
        totalSupply: '',
        withdrawAmount: ''
      }
    },
    methods: {
      async setPrice() {
        //重设价格 1eth = 68000 els
        this.flag = false
        this.isLoading = true
        let addr = this.$store.state.addr
        let instance = this.$store.state.exchangeinstance
        await instance.setPrice(this.newPrice, {from: addr, gasPrice: this.gasPrice})
        await this.getBalance()
        await this.getExchangeDone()
        this.newPrice = ''
        this.isLoading = false
        this.flag = true
      },
      async getBalance(){
        let instance = this.$store.state.walletinstance
        let b = await instance.totalSupply()
        this.totalSupply = this.weiToEth(b)
      },
      async getExchangeDone(){
        let instance2 = this.$store.state.exchangeinstance
        //兑换价格
        let a5 = await instance2.getMessage()
        this.price = a5[1]
      },
      async withdrawLark(){
        this.flag = false
        this.isLoading = true
        let addr = this.$store.state.addr
        let instance = this.$store.state.exchangeinstance
        let amount2 = this.web3.utils.toWei(this.withdrawAmount, 'ether')
        await instance.withdrawLark(amount2, {from: addr, gasPrice: this.gasPrice})
        await this.getBalance()
        await this.getExchangeDone()
        this.withdrawAmount = ''
        this.isLoading = false
        this.flag = true
      },
    },

    components: {
        Loading
      },
    mounted() {
      let that = this
      let instance = this.$store.state.walletinstance
      async  function main(){
        if(Object.keys(instance).length === 0){
          //如果刷新页面, instance未定义
          // console.log(888, "instance为空，是刷新页面")
          await that.getWalletInstance()
          await that.getExchangeInstance()
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        } else{
          // console.log(22333, "啥也没干！")
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        }
      }
      main()
    },

    }
</script>

<style scoped>

  .login{
    margin: 4rem 4rem 4rem 15rem;
  }
  .user{
    margin-top: 1rem;
    width: 30rem;
  }
  .user2{
    display: inline;
    width: 8rem;
  }

  .exchange{
    width: 30rem;
  }
  .loginbtn{
    margin-top: 0.5rem;
  }
</style>

