<template>
  <div class="article">
    <b-input
            id="inline-form-input-name"
            class="mb-2 mr-sm-2 mb-sm-0 search"
            placeholder="搜索" v-model="keywords"
    ></b-input>

    <div class="orderlist">
      <!--列表-->
      <b-container class="bv-example-row">
        <b-row >
          <b-col sm="12" md="6" lg="4" v-for="bounty in search(keywords)">
            <div class="content" >
                <router-link :to="'/bounty/'+bounty.taskNumber">
                <b-img :src="bounty.content | takeImage" fluid  class="orderimg"></b-img>
               </router-link>
              <router-link :to="'/bounty/'+bounty.taskNumber">
                <span class="title">{{ titleFormat(bounty.name) }}</span>
              </router-link>

              <div class="text">
                <span>金主： @{{  bounty.nickname }} ({{ bounty.promoter | formatAddr  }})</span><br>
                <!--猎人A： {{ bounty.hunterA| formatAddr  }} <br>-->
                赏金： {{ weiToEth(bounty.bonus) }}  灵石<br>
                任务： {{ bounty.content | abstractFn }}......<br>
                <span>发布时间：{{ timeFormat(bounty.created) }} </span><br>
                任务时间： {{ formatSeconds(bounty.taskDeadLine) }}<br>

              </div>
            </div>
          </b-col>
        </b-row>
      </b-container>

      <div style="clear:both;"></div>
    </div>

    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

    <!--<div class="btn">-->
    <!--&lt;!&ndash;<b-button variant="outline-primary" @click="getNext">查看更多</b-button>&ndash;&gt;-->
    <!--</div>-->
  </div>
</template>

<script>
  import Loading from './Loading'
  export default {
    name: "Index",
    data(){
      return{
        keywords:'',
        isLoading: true,
      }
    },
    methods:{
      search(keywords){
        let bountys = this.$store.state.bountys
        return bountys.filter(item => {
          if(item.state === '0' && item.name.includes(keywords)){
            // console.log(166, '查到了！')
            return item
          }
        })
      },
      getNext(){
        let that = this
        that.isLoading = true
        let query = {tag: "hive-180932", limit: 20, start_author: this.author, start_permlink: this.permlink}
        this.steem.api.getDiscussionsByTrending(query, function(err, data) {
          data.forEach(post => {
            if (post.permlink != that.permlink) {
              that.posts.push(post)
            }
          })
          //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
          that.author = data[data.length - 1].author
          that.permlink = data[data.length - 1].permlink
          //将得到的数据存入vuex中
          that.$store.commit('saveHot', that.posts)
          that.isLoading = false
          if(data.length == 1){
            alert('没有更多了！')
          }
        })
      },
    },
    computed: {

    },

    components: {
      Loading
    },

    mounted() {
      let that = this
      let instance = this.$store.state.bountyinstance
      async  function main(){
        if(Object.keys(instance).length === 0){
          //如果刷新页面, instance未定义
          // console.log(888, "instance为空，是刷新页面")
          let instance = await that.getBountyInstance()
          await that.getTasks(instance)
          that.isLoading = false
         } else{
          if(that.$store.state.bountys == false){
            // console.log(6647, "如果悬赏更新！")
            //如果悬赏更新
            await that.getTasks(instance)
            that.isLoading = false
          } else{
            // console.log(22333, "啥也没干！")
            that.isLoading = false
          }
        }
      }
      main()
    },

    filters:{
      formatAddr(data){
        return data.substring(0,6)+'...'+data.substring(38)
      },
      takeImage(text){
        let reg = /!\[(.*?)\]\((.*?)\)/
        // let reg = /\!\[.*?\)/
        let a = text.match(reg)
        if(a === null){
          return 'https://steemjiang.com:8081/ipfs/QmegAj9BdJ1HGhZSJtFs4WUAen82aMQtcbnoxujd6YMQ5o'
        }
        return a[2]
      },
      abstractFn(res){
        if(!res){
          return ''
        }else{
          let str=res.replace(/(\*\*|__)(.*?)(\*\*|__)/g,'')          //全局匹配内粗体
                  .replace(/\!\[[\s\S]*?\]\([\s\S]*?\)/g,'')           //全局匹配图片
                  .replace(/\([\s\S]*?\)/g,'')                              //全局匹配连接
                  .replace(/<\/?.+?\/?>/g,'')                             //全局匹配内html标签
                  .replace(/http.*\w/g,'')                              //全局http连接
                  .replace(/(\*)(.*?)(\*)/g,'')                               //全局匹配内联代码块
                  .replace(/`{1,2}[^`](.*?)`{1,2}/g,'')                       //全局匹配内联代码块
                  .replace(/```([\s\S]*?)```[\s]*/g,'')                       //全局匹配代码块
                  .replace(/\~\~(.*?)\~\~/g,'')                               //全局匹配删除线
                  .replace(/[\s]*[-\*\+]+(.*)/g,'')                           //全局匹配无序列表
                  .replace(/[\s]*[0-9]+\.(.*)/g,'')                           //全局匹配有序列表
                  .replace(/(#+)(.*)/g,'')                                    //全局匹配标题
                  .replace(/(>+)(.*)/g,'')                                    //全局匹配摘要
                  .replace(/\r\n/g,"")                                        //全局匹配换行
                  .replace(/\n/g,"")                                          //全局匹配换行
          // .replace(/\s/g,"")                                          //全局匹配空字符;
          return str.slice(0,100)
        }
      },
    },
  }
</script>

