const getArticles = function(instance){
  let that = this
  return new Promise(resolve => {
    async function main(){
      let articles = []
      let res = await instance.getPastEvents('PostArticle', {fromBlock: 0, toBlock: 'latest'})
      // console.log(1234, res)
      // console.log(1365, res.length)
      for(let i = res.length-1; i >=0; i--){
          let con = await that.ipfs.cat(res[i].returnValues.hash)
          let t = that.utf8ToStr(con)
          let article = JSON.parse(t)
          articles.push(article)
      }
      //将得到的数据存入vuex中
      that.$store.commit('saveArticles', articles)
      resolve()
    }
    main()
  })
}

export {getArticles}
