<template>
  <div class="article">
     <div class="content" >

        <p class="posttime">
            <span style="margin-right: 1.5rem;margin-left: 2rem">
            悬赏号：{{ task.taskNumber }}</span>
            {{ timeFormat(task.created) }}
        </p>
        <p class="title">{{ task.name}}</p>
         <div class="text">
             金主： @{{  task.nickname }} ({{ task.promoter }})<br>
             赏金： <span v-if="task.bonus">{{ weiToEth(task.bonus)  }} 灵石<br></span>
             联系金主：{{ task.contact }}<br>
             任务描述：
             <mavon-editor
                     :value="task.content + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>
             <br>
         </div>

         <!--任务申请中-->
         <div v-if="task.state === '0'">
             <div class="btn2" v-if="cancellFlag">悬赏申请中...... <br><br>
                <b-button variant="outline-primary" @click="cancelTask(task.uid)">取消悬赏</b-button>
             </div>
             <div class="btn3" v-else>您已取消，等待网络确认！</div>
              <b-form-group :label="'已申请的猎人'+key" v-for="(hunter,key) in task.hunters" class="hunter">
                 <b-form-radio v-model="selected" name="some-radios" :value="hunter">{{ hunter }}</b-form-radio>
                 <div class="reason" v-if="task.reasons[key]">
                     <p><strong>申请理由：</strong>{{ task.reasons[key].reason }} </p>
                     <strong>联系方式：</strong>{{ task.reasons[key].hunterContact }}
                 </div>
             </b-form-group>

             <div class="mt-3 hunter" v-if="selected" >中选猎人:
                 <strong>{{ selected }}</strong>
             </div>
             <br>
             <div class="btn3" v-if="selected">
                 <div v-if="confirmFlag">
                 <b-button variant="outline-primary" @click="confirmHunter(selected, task.uid)">确认猎人</b-button>
                 </div>
                 <div class="btn3"  v-else>您已提交，请等待网络确认！</div>
             </div>
             <br>
         </div>

         <!--悬赏已取消-->
         <div v-else-if="task.state  === '1'">
             <span class="btn2" >悬赏已取消！ </span>
         </div>

          <!--任务进行中-->
         <div v-else-if="task.state  === '2'">
             <span class="btn2" v-if="leftTime > 0">任务进行中..... </span>
             <div class="mt-3 reason">
                 <strong>中选猎人: {{ task.hunterA}}</strong>
                 <p><strong>申请理由：</strong>{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>

             <div v-if="leftTime > 0" class="reason">
                 <p style="color: darkred;">任务剩余时间： {{ leftTimeNow }}</p>
                 <p style="text-align: center;">等待中选猎人提交任务！</p>
             </div>
             <div v-else class="btn3">任务已超时！<br>
                 <div class="btn" v-if="outOfTimeFlag2">
                     <b-button variant="outline-primary" @click="hunterAOutOfTime(task.uid)">猎人超时未提交，悬赏提前结束！</b-button>
                 </div>
                 <div class="btn3" v-else>您已提交，等待网络确认！</div>
             </div>
         </div>

         <!--猎人已提交任务-->
         <div v-else-if="task.state  === '3'">
             <div class="mt-3 reason">
                 <strong>中选猎人: {{ task.hunterA}}</strong>
                 <p><strong>申请理由：</strong>{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>
             <span class="btn2">猎人已提交任务..... </span>
             <div v-if="leftTime > 0" class="btn2">
                 <p style="color: darkred;">金主确认的剩余时间： {{ leftTimeNow }} </p>
                 <div class="btn3" v-if="finishedandfallFlag">
                     <b-button variant="outline-primary" @click="promoterFinished(task.uid)" style="margin-right: 1rem">
                         确认任务完成
                     </b-button>
                     <b-button variant="outline-primary" @click="taskFallback(task.uid)">退回任务</b-button>
                 </div>
                 <div class="btn3" v-else>您已提交，等待网络确认！</div>
             </div>
             <div v-else class="btn3">您已超时未确认！</div>
         </div>

         <!--猎人未提交，任务提前结束-->
         <div v-else-if="task.state  === '4'">
             <div class="mt-3 reason">
                 <strong>中选猎人: {{ task.hunterA}}</strong>
                 <p><strong>申请理由：</strong>{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>
             <div class="btn2">猎人未提交，悬赏提前结束！</div>
         </div>

         <!--任务完成-->
         <div v-else-if="task.state  === '5'">
             <div class="mt-3 reason">
                 <strong>中选猎人: {{ task.hunterA}}</strong>
                 <p><strong>申请理由：</strong>{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>
             <div class="btn2">悬赏圆满完成！</div>
         </div>

         <!--金主未确认，悬赏强制完成！-->
         <div v-else="task.state  === '6'">
             <div class="mt-3 reason">
                 <strong>中选猎人: {{ task.hunterA}}</strong>
                 <p><strong>申请理由：</strong>{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>
             <div class="btn2">您超时未确认，悬赏被猎人强制完成！</div>
         </div>

    </div>

      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
       name: "MyBountyUnit",
      data(){
          return{
            task: {},
            selected: '',
            isLoading: false,
            hunterMessage: {},
            cancellFlag: true,
            confirmFlag: true,
            outOfTimeFlag2: true,
            finishedandfallFlag: true,
            timestamp: ''
          }
      },
      methods:{
         taskFilter(){
              //获取单个任务
              let tasks = this.$store.state.bountys
              let taskNumber = this.$route.params.taskNumber
              // let instance = this.$store.state.bountyinstance
              tasks.forEach(item => {
                  if(item.taskNumber === taskNumber){
                      this.task = item

                      //任务剩余时间
                      // let deadtime = parseInt(item.taskStart) + parseInt(item.taskDeadLine)
                      // let timestamp = Date.parse(new Date()) * 1e-3
                      // this.leftTime = deadtime - timestamp
                  }
              })

             //任务剩余时间
             // this.leftTime = await instance.getLeftTime(this.task.uid)
          },
         getHunterMessage(address){
             let task = this.task
             for(let i = 0; i < task.hunters.length; i++){
                 if(task.hunters[i] === address){
                     // console.log(111, "找到啦 ")
                     // console.log(1111, task.reasons[i])
                     return task.reasons[i]
                 }else {
                     // console.log(222, "没找到啦 ")
                 }
             }
          },
         async confirmHunter(selected, id){
             console.log(222)
             await this.unlockWallet()
             this.confirmFlag = false
             let addr = this.$store.state.addr
             let instance = this.$store.state.bountyinstance
             await instance.confirmHunter(id, selected, {from: addr, gasPrice: this.gasPrice})
             await this.getTasks(instance)
             this.taskFilter()
          },
         async cancelTask(id){
             await this.unlockWallet()
             this.cancellFlag = false
              let addr = this.$store.state.addr
              let instance = this.$store.state.bountyinstance
              await instance.cancelTask(id, {from: addr, gasPrice: this.gasPrice})
              await this.getTasks(instance)
              this.taskFilter()
          },
         async promoterFinished(id){
             await this.unlockWallet()
              this.finishedandfallFlag = false
              let addr = this.$store.state.addr
              let instance = this.$store.state.bountyinstance
              await instance.promoterFinished(id, {from: addr, gasPrice: this.gasPrice})
              await this.getTasks(instance)
              this.taskFilter()
          },
         async taskFallback(id){
             await this.unlockWallet()
              this.finishedandfallFlag = false
              let addr = this.$store.state.addr
              let instance = this.$store.state.bountyinstance
              await instance.taskFallback(id, {from: addr, gasPrice: this.gasPrice})
              await this.getTasks(instance)
              this.taskFilter()
          },
         async hunterAOutOfTime(id){
             await this.unlockWallet()
             this.outOfTimeFlag2 = false
              let instance = this.$store.state.bountyinstance
              let addr = this.$store.state.addr
              await instance.hunterAOutOfTime(id, {from: addr, gasPrice: this.gasPrice})
              await this.getTasks(instance)
              this.taskFilter()
          },
      },
      components: {
        Loading
      },
      computed: {
          prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },
          leftTime(){
              //任务剩余时间
              let deadtime = parseInt(this.task.taskStart) + parseInt(this.task.taskDeadLine)
              return deadtime - this.timestamp
          },
          leftTimeNow(){
              //任务剩余时间
              let deadtime = parseInt(this.task.taskStart) + parseInt(this.task.taskDeadLine)
              let s =  deadtime - this.timestamp
              return this.formatSeconds(s)
          }
        },


        mounted() {
            let that = this
            let instance = this.$store.state.bountyinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    console.log(822, "instance为空，是刷新页面")
                    let instance = await that.getBountyInstance()
                    await that.getTasks(instance)
                    that.taskFilter()
                    that.isLoading = false
                } else{
                    if(that.$store.state.bountys == false){
                        // console.log(6647, "如果悬赏更新！")
                        //如果文章更新
                        await that.getTasks(instance)
                        that.taskFilter()
                        that.isLoading = false
                    } else{
                        // console.log(22333, "啥也没干！")
                        that.taskFilter()
                        that.isLoading = false
                    }
                }
            }
            main()

            let getTimestamp = function(){
                that.timestamp = Date.parse(new Date()) * 1e-3
            }
            setInterval(getTimestamp, 1000)
        },

      filters:{

      },
    }
</script>

