const getBalanceAndAllowance = function(){
  let that = this
  return new Promise(resolve => {
    async function main(){
      let addr = that.$store.state.addr
      let instance = that.$store.state.walletinstance
      let a = await instance.balanceOf(addr)
      let b = that.web3.utils.fromWei(a, 'ether')
      //将得到的数据存入vuex中
      that.$store.commit('saveBalanceOf', b)

      let t = await instance.allowance(addr, that.tokenproxy)
      let s = that.web3.utils.fromWei(t, 'ether')
      //将得到的数据存入vuex中
      that.$store.commit('saveAllowance', s)
      resolve()
    }
    main()
  })
}

export {getBalanceAndAllowance}
