<template>
  <div id="maintop">
    <div  v-if="flag">
    <b-input
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="请输入名称" v-model="name"
    ></b-input>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 title"
              :class="checkBonusFlag ? 'isok': 'isfalse'"
              placeholder="请输入赏金（灵石数）" v-model="bonus"
              @keyup="checkBonus"
      ></b-input>
      <strong style="color: rgba(240,56,6,0.75)">加上押金 {{ bonus/2 }} 灵石</strong>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 content"
              placeholder="请输入联系方式，只有中标的猎人可见。"  v-model="contact"
      ></b-input>

      任务描述：
    <div id="main">
          <mavon-editor v-model="content" ref="md" @imgAdd="uploadimg" style="height: 100%"/>
    </div>

      <br>
      设定任务时间：
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              :class="checkHourFlag ? 'isok': 'isfalse'"
              placeholder="请输入小时" v-model="hour"
              @keyup="checkHour"
      ></b-input>
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              :class="checkMinuteFlag ? 'isok': 'isfalse'"
              placeholder="请输入分钟" v-model="minute"
              @keyup="checkMinute"
      ></b-input>
      <br>
      使用费： 0.5%<br>

    <div v-if="isApprove" >
      <div v-if="approveFlag">
      <b-button variant="outline-primary" @click="letapprove" class="btn">授权 </b-button>
      <span style="color: gray;padding-left: 1rem;">发布悬赏</span><br><br>
      </div>
      <div  v-else style="margin-top: 0.8rem;margin-bottom: 1.5rem">
        您已提交，请等待网络确认！
      </div>
    </div>
    <div class="btn" v-else>
      <div v-if="checkBonusFlag && checkHourFlag && checkMinuteFlag">
          <b-button variant="outline-primary" @click="post">发布悬赏</b-button><br>
      </div>
      <div v-else>输入有误，请重新输入！</div>
      <br>
    </div>

    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "PostBounty",
      data() {
        return {
          db:{},
          content: '',
          name: '',
          contact: '',
          bonus: '',
          checkBonusFlag: true,
          hour: '',
          minute: '',
          checkHourFlag: true,
          checkMinuteFlag: true,
          isLoading: false,
          flag: false,
          approveFlag: true
        }
      },

      methods: {
        async letapprove(){
          this.approveFlag = false
          await this.approve()
          await this.getBalanceAndAllowance()
        },
        checkBonus(){
          let reg = /^\d+(\.\d+)?$/
          let res = reg.test(this.bonus)
          this.checkBonusFlag = res
        },
        checkHour(){
          let reg = /^[0-9]+$/
          let res = reg.test(this.hour)
          this.checkHourFlag = res
        },
        checkMinute(){
          let reg = /^[0-9]+$/
          let res = reg.test(this.minute)
          this.checkMinuteFlag = res
        },
        async post(){
            await this.unlockWallet()
            if(this.name !== '' && this.contact !== '' && this.content !== ''){
              if(parseFloat(this.$store.state.balanceOf) >= parseFloat(this.bonus * 1.5) ){
                this.flag = false
                this.isLoading = true
                let nickname = this.$store.state.nickname
                let instance = this.$store.state.bountyinstance
                let addr = this.$store.state.addr
                let name= this.name
                let taskNumber = this.getNum()
                let bonus= this.bonus
                let contact = this.contact
                let content = this.content
                let hour = this.hour
                let minute = this.minute
                let created = this.getTimeStamp()
                let value = this.web3.utils.toWei(bonus, 'ether')
                let task = {
                  "name": name,
                  "nickname": nickname,
                  "taskNumber": taskNumber,
                  "contact": contact,
                  "content": content,
                  'bonus': value,
                  'created': created
                }
                //上传内容到ipfs
                let con = Buffer.from(JSON.stringify(task))
                let res = await this.ipfs.add(con)
                let hash = res[0].hash
                await instance.setTask(hash, value, hour, minute, {from: addr, gasPrice: this.gasPrice})
                await this.getBalanceAndAllowance()
                //清空vuex中的数据
                this.$store.commit('saveBountys', [])
                this.$router.push({path:'/'})

              }else{
                alert("您的灵石余额不足！")
              }

            }else{
              alert("名称、联系、描述等不能为空，请填写！")
            }
          },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        },

      },

      computed: {
        isApprove() {
          return  parseFloat(this.$store.state.allowance) < parseFloat(this.bonus)
        }
      },

      components: {
        Loading
      },

      mounted() {
        let that = this
        let instance = this.$store.state.bountyinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //如果刷新页面, instance未定义
            // console.log(888, "instance为空，是刷新页面")
            let instance = await that.getBountyInstance()
            await that.getTasks(instance)
            that.isLoading = false
            that.flag = true
          } else{
            if(that.$store.state.bountys == false){
              // console.log(6647, "如果悬赏更新！")
              //如果悬赏更新
              await that.getTasks(instance)
              that.isLoading = false
              that.flag = true
            } else{
              // console.log(22333, "啥也没干！")
              that.isLoading = false
              that.flag = true
            }
          }

          //查询余额和授权
          if(that.$store.state.allowance === ''){
            // console.log(111, "查授权")
            await that.getBalanceAndAllowance()
          }else{
            // console.log(222, "已查授权")
          }

        }
        main()
      },
    }
</script>

