<template>
  <div id="maintop">
    <div  v-if="flag">
    <b-input
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="请输入商品名称" v-model="name"
    ></b-input>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 title"
              :class="checkPriceFlag ? 'isok': 'isfalse'"
              placeholder="请输入价格：灵石" v-model="price"
              @keyup="checkPrice"
      ></b-input>

      <b-form-textarea
              class="content"
              v-model="content"
              placeholder="请输入电子商品的链接或网盘地址，买家付费后可见！"
              rows="3"
              max-rows="6"
      ></b-form-textarea>
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 content"
              placeholder="请输入联系方式，买家付费后可见。"  v-model="contact"
      ></b-input>

      商品描述：
    <div id="main">
          <mavon-editor v-model="body" ref="md" @imgAdd="uploadimg" style="height: 100%"/>
    </div>
      <br>使用费： 0.5%<br>


    <div class="btn">
      <div v-if="checkPriceFlag">
        <b-button variant="outline-primary" @click="post">提交</b-button>
      </div>
      <div v-else>输入有误，请重新输入！</div>
    </div><br><br>

    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "PostOrder",
      data() {
        return {
          db:{},
          body: '',
          content: '',
          name: '',
          price: '',
          contact: '',
          isLoading: false,
          flag: false,
          checkPriceFlag: true
        }
      },

      methods: {
        checkPrice(){
          let reg = /^\d+(\.\d+)?$/
          let res = reg.test(this.price)
          this.checkPriceFlag = res
        },
        async post(){
          await this.unlockWallet()
          if(this.name !== '' && this.content !== '' && this.contact !== '' && this.body !== '') {
            this.flag = false
            this.isLoading = true
            let nickname = this.$store.state.nickname
            let instance = this.$store.state.ordertinstance
            let addr = this.$store.state.addr
            let name = this.name
            let orderNumber = this.getNum()
            let price = this.price
            //加密
            let content = this.encypt(this.content)
            let contact = this.encypt(this.contact)

            let body = this.body
            let created = this.getTimeStamp()
            let value = this.web3.utils.toWei(price, 'ether')
            let order = {
              "name": name,
              "nickname": nickname,
              "addr": addr,
              "orderNumber": orderNumber,
              "content": content,
              "contact": contact,
              "body": body,
              'price': value,
              'created': created
            }
            //上传内容到ipfs
            let con = Buffer.from(JSON.stringify(order))
            let res = await this.ipfs.add(con)
            let hash = res[0].hash
            await instance.postOrder(hash, value, {from: addr, gasPrice: this.gasPrice})
            //清空vuex中的数据
            this.$store.commit('saveOrders', [])
            this.$router.push({path: '/market'})
          }else{
            alert("名称、链接、描述等不能为空，请填写！")
          }
          },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        }
      },

      components: {
        Loading
      },

      mounted() {
        let that = this
        let instance = this.$store.state.ordertinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //如果刷新页面, instance未定义
            // console.log(888, "instance为空，是刷新页面")
            let instance = await that.getOrderInstance()
            await that.getOrders(instance)
            that.isLoading = false
            that.flag = true
          } else{
            if(that.$store.state.orders == false){
              // console.log(667, "如果文章更新！")
              //如果文章更新
              await that.getOrders(instance)
              that.isLoading = false
              that.flag = true
            } else{
              // console.log(2233, "啥也没干！")
              that.isLoading = false
              that.flag = true
            }
          }
        }
        main()
      },
    }
</script>

