const unlockWallet = async function(){
    // let accounts = await window.ethereum.enable()
    let accounts = await ethereum.request({ method: 'eth_requestAccounts' })
    // console.log(11111, accounts)
    let addr = accounts[0]
    // console.log(124, addr)
    if(addr !== this.$store.state.addr){
      let nickname = this.$store.state.nickname
      this.$store.commit('saveUser', {nickname: nickname, addr: addr})
      // console.log(563, "更新地址！")
    }
}

export {unlockWallet}
