<template>
  <div class="article">
     <div class="content" >

        <p class="posttime">
            <span style="margin-right: 1.5rem;margin-left: 2rem">
                悬赏号：{{ task.taskNumber }}</span>
                {{ timeFormat(task.created) }}
        </p>

         <p class="title">{{ task.name}}</p>

         <div class="text">
            金主： @{{  task.nickname }} ({{ task.promoter }})<br>
            赏金：<span v-if="task.bonus">{{ weiToEth(task.bonus)  }}  灵石<br></span>
            联系金主：{{ task.contact }}<br>
            任务描述：
             <mavon-editor
                     :value="task.content + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>
         </div>

         <!--正在申请的任务-->
         <div v-if="task.state === '0'"  class="reason">
             <span v-if="task.hunters != false">
                 <br>已申请的猎人名单：<br>
                 <span style="font-size: 0.9rem;">{{ task.hunters }}</span>
                 <br><br>
             </span>
             <p><strong>申请理由：</strong>{{ getHunterMessage().reason }}</p>
             <p><strong>联系方式：</strong>{{ getHunterMessage().hunterContact }}</p>
             <div class="btn2">您已申请！</div>
             <div class="btn2">任务申请中......</div>
         </div>

         <!--任务进行中-->
         <div v-else-if="task.state === '2' && task.hunterA.toLowerCase() === $store.state.addr">
             <div v-if="leftTime > 0">
                 <div class="reason"><br>
                     <p><strong>申请理由：</strong>{{ getHunterMessage().reason }}</p>
                     <p><strong>联系方式：</strong>{{ getHunterMessage().hunterContact }}</p>
                 </div>
                 <div class="btn2 text">
                 任务进行中.....
                 <div style="color: darkred;" >
                     任务剩余时间： {{ leftTimeNow}}
                 </div>
                 <div class="btn" v-if="finishedFlag">
                     <b-button variant="outline-primary" @click="hunterAFinished(task.uid)">提交任务</b-button>
                 </div>
                 <div class="btn3" v-else>您已提交，等待网络确认！</div>
                 </div>
             </div>
             <div v-else class="btn2">任务已超时！</div>
         </div>

         <!--猎人已提交任务-->
         <div v-else-if="task.state === '3' && task.hunterA.toLowerCase() === $store.state.addr" >
             <div class="reason">
                 <p><strong>申请理由：</strong>{{ getHunterMessage().reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage().hunterContact }}</p>
             </div>
             <div class="btn2">
                 <br> 您已提交任务，请等待金主确认或是退回！<br><br>
                 <div v-if="leftTime > 0" style="color: darkred;">金主确认的剩余时间： {{ leftTimeNow  }}</div>
                 <div v-else>
                     <div class="btn" v-if="outOfTimeFlag">
                         <b-button variant="outline-primary" @click="promoterOutOfTime(task.uid)">
                             金主超时未确认，强制完成悬赏</b-button>
                     </div>
                     <div class="btn3" v-else>您已提交，等待网络确认！</div>
                 </div>
             </div>
         </div>

         <!--猎人未提交，任务提前结束-->
         <div v-else-if="task.state  === '4' && task.hunterA.toLowerCase() === $store.state.addr">
             <div class="reason">
                 <br><p><strong>申请理由：</strong>{{ getHunterMessage().reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage().hunterContact }}</p>
             </div>
             <div class="btn2">您超时未提交，任务提前结束！</div>
         </div>


         <!--金主已确认-->
         <div  v-else-if="task.state === '5' && task.hunterA.toLowerCase() === $store.state.addr" ><br>
             <div class="reason">
                 <p><strong>申请理由：</strong>{{ getHunterMessage().reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage().hunterContact }}</p>
             </div>
             <div class="btn2">金主已确认，恭喜您圆满完成任务并收到赏金！</div>
         </div>

         <!--金主未确认，悬赏强制完成！-->
         <div v-else-if="task.state  === '6' && task.hunterA.toLowerCase() === $store.state.addr">
             <div class="reason">
                 <p><strong>申请理由：</strong>{{ getHunterMessage().reason }}</p>
                 <p><strong>联系方式：</strong>{{ getHunterMessage().hunterContact }}</p>
             </div>
             <div class="btn2">金主超时未确认，您已强制完成悬赏！</div>
         </div>

         <!--落选的任务！-->
         <div v-else="task.state  !== '0' && task.hunterA.toLowerCase() !== $store.state.addr && huntersFor(task)" class="reason">
             <br><p><strong>申请理由：</strong>{{ getHunterMessage().reason }}</p>
             <p><strong>联系方式：</strong>{{ getHunterMessage().hunterContact }}</p>
             <div class="btn2">落选的任务！</div>
         </div>

     </div>


      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "MyTaskUnit",
      data(){
          return{
            task: {},
            selected: '',
            isLoading: false,
            finishedFlag: true,
            outOfTimeFlag: true,
            timestamp: ''
          }
      },
      methods:{
         async taskFilter(){
              //获取单个任务
              let tasks = this.$store.state.bountys
              let taskNumber = this.$route.params.taskNumber
              // let instance = this.$store.state.bountyinstance
              tasks.forEach(item => {
                  if(item.taskNumber === taskNumber){
                      this.task = item

                      //任务剩余时间
                      // let deadtime = parseInt(item.taskStart) + parseInt(item.taskDeadLine)
                      // let timestamp = Date.parse(new Date()) * 1e-3
                      // this.leftTime = deadtime - timestamp
                  }
              })
              //任务剩余时间
              // this.leftTime = await instance.getLeftTime(this.task.uid)
          },
         getHunterMessage(){
             let addr = this.$store.state.addr
              let task = this.task
              for(let i = 0; i < task.hunters.length; i ++){
                  if(task.hunters[i].toLowerCase() === addr){
                      // console.log(111, "找到啦 ")
                      // console.log(1111, task.reasons[i])
                      return task.reasons[i]
                  }else {
                      // console.log(222, "没找到啦 ")
                  }
              }
          },
         async hunterAFinished(id){
             await this.unlockWallet()
             this.finishedFlag = false
             let instance = this.$store.state.bountyinstance
             let addr = this.$store.state.addr
             await instance.hunterAFinished(id, {from: addr, gasPrice: this.gasPrice})
             await this.getTasks(instance)
             this.taskFilter()
          },
         async promoterOutOfTime(id){
             await this.unlockWallet()
             this.outOfTimeFlag = false
             let instance = this.$store.state.bountyinstance
             let addr = this.$store.state.addr
             await instance.promoterOutOfTime(id, {from: addr, gasPrice: this.gasPrice})
             await this.getTasks(instance)
             this.taskFilter()
          },
         huntersFor(task){
             for(let i = 0; i < task.hunters.length; i ++){
                 if(task.hunters[i].toLowerCase() === addr){
                     return true
                 }
             }
          }
      },


      components: {
        Loading
      },
      computed: {
          prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },
          leftTime(){
              //任务剩余时间
              let deadtime = parseInt(this.task.taskStart) + parseInt(this.task.taskDeadLine)
              return deadtime - this.timestamp
          },
          leftTimeNow(){
              //任务剩余时间
              let deadtime = parseInt(this.task.taskStart) + parseInt(this.task.taskDeadLine)
              let s =  deadtime - this.timestamp
              return this.formatSeconds(s)
          }
        },

        mounted() {
            let that = this
            let instance = this.$store.state.bountyinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    // console.log(888, "instance为空，是刷新页面")
                    let instance = await that.getBountyInstance()
                    await that.getTasks(instance)
                    that.taskFilter()
                    // await that.hashToString()
                    that.isLoading = false
                } else{
                    if(that.$store.state.bountys == false){
                        // console.log(6647, "如果悬赏更新！")
                        //如果文章更新
                        await that.getTasks(instance)
                        that.taskFilter()
                        // await that.hashToString()
                        that.isLoading = false
                    } else{
                        // console.log(22333, "啥也没干！")
                        that.taskFilter()
                        // await that.hashToString()
                        that.isLoading = false
                    }
                }
            }
            main()

            let getTimestamp = function(){
                that.timestamp = Date.parse(new Date()) * 1e-3
            }
            setInterval(getTimestamp, 1000)
        },

      filters:{

      },
    }
</script>

<style scoped>

  .article{
    margin-top: 3rem;
  }
  .posttime{
    font-size: 0.8rem;
    display: inline;
  }
  .content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 3rem auto;
